* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

button {
    background-color: rgb(236, 236, 235);
    box-shadow: 1px 2px 4px -3px;
    border: 1px double rgb(110, 67, 18);
    padding: 5px;
    cursor: pointer;
}

button:hover {
    box-shadow: 0 4px 8px 0 rgba(110, 67, 18, 0.445);
}

h1 {
    color: rgb(110, 67, 18);
    font-style: italic;
    margin-bottom: 15px;
    text-align: center;
}
h2 {
    color: rgb(110, 67, 18);
    font-style: italic;
    margin-bottom: 15px;
    text-align: center;
}

.app {
    position: relative;
    min-height: 100vh;
    padding: 2rem 3rem;
    max-width: 800px;
    margin: auto;
    background: linear-gradient(to top, rgb(255, 255, 255), rgba(4, 226, 255, 0.281));
}

.todo-sheet {
    border: 7px double rgb(110, 67, 18);
    background-color: rgb(255, 255, 255);
    box-shadow: 1px 2px 4px -1px;
    margin: 10px 20px 10px;
    min-height: 90vh;
    padding: 20px;
    position: relative
}

.error {
    color: red;
    font-size: 20px;
    background-color: ;
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
    text-align: center;
    font-style: italic;
    font-weight: 700;
}

.btn-status-div {
    display: flex;
    justify-content: right;
}

.login-div {
    max-width: 500px;
    max-height: 300px;
    padding: 20px;
    margin: auto;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    text-align: center;
    border: 3px double rgb(110, 67, 18);
