}
.login-div p {
    margin: 5px;
}


.toggle-button {
    width: 120px;
    margin: 1rem 1rem 0rem;
    padding: 5px 15px;
}

.show-btn {
    margin: 2rem 1rem 0rem 1rem;
    padding: 10px 5px;
}

.login-btn {
    width: 120px;
    margin: 1rem 1rem 0rem;
    padding: 5px 15px;
}
.cancel-button {
    width: 120px;
    margin: 1rem 1rem 0rem;
    padding: 5px 15px;
    background-color: rgba(250, 80, 80, 0.651);
}

.landing-page {
    padding: 40px;
}

.landing-page li {
    border-bottom: 1px double rgba(110, 67, 18, 0.493);
    background-size: 100% 80px;
    background-repeat: repeat;
    margin-bottom: 20px;
    margin-top: 50px;
}

.loading-div {
    margin-top: 150px;
    text-align: center;
}

.task-container {
    display: flex;
    font-size: 20px;
    color: rgb(11, 48, 46);
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    min-height: 60px;
    border: 3px double rgb(110, 67, 18);
    margin: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.task-container:hover {
    box-shadow: 0 4px 8px 0 rgba(110, 67, 18, 0.445);
}

.completed {
    text-decoration: line-through;
    color: aliceblue;
    background-color: rgba(126, 21, 21, 0.37);
}

.new-task-form {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.new-task-form button {
    margin: 15px auto 0px;
    width: 120px;
}

.new-task-textbox {
    height: 30px;
    text-align: center;
    margin: 0px 10px;
}

.create-div {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    justify-content: center;
    text-align: center;
}

.delete-btn-div {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.delete-btn {
    border: 1px double;
    width: 100px;
    height: 40px;
    margin: 1rem 1rem 0rem;
    padding: 5px 15px;
    font-weight: 600;
    background-color: rgba(126, 21, 21, 0.37);
}

.remove-task-btn {
    border: 1px double;
    font-size: 10px;
    width: 60px;
    height: 30px;
    padding: 1px;
    font-weight: 600;
    margin-bottom: 3px;
    background-color: rgba(126, 21, 21, 0.37);
}

.modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.modal-content {
    background-color: rgb(236, 236, 235);
    border: 3px double rgb(110, 67, 18);
    color: black;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 20px;
    cursor: pointer;
}

.modal-content button {
    border: 1px double;
    width: 100px;
    height: 40px;
    margin: 1rem 1rem 0rem;
    padding: 5px 15px;
    font-weight: 600;
}

#confirm-delete-btn {
    background-color: rgba(126, 21, 21, 0.37);
}


/* Customize the label (the container) */
.container {
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    font-size: 22px;
}

/* Hide the browser's default checkbox */
.container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 1;
    left: 0;
    height: 30px;
    width: 30px;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */
.container:hover input~.checkmark {
    background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.container input:checked~.checkmark {
    background-color: #ccc;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: relative;
    display: none;
}

/* Show the checkmark when checked */
.container input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
    top: 10%;
    left: 32%;
    transform: translate(-50%, -50%);
    width: 8px;
    height: 16px;
    border: solid rgb(67, 90, 90);
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
}

.task-span {
    margin-left: 20px;
    font-size: 24px;
}




/* Mobile */
@media (max-width: 768px) {
    .app {
        padding: 5px 0;
    }
    .task-span {
        font-size: 24px;
        margin-left: 0;
    }
    .todo-sheet {
        padding: 20px
    }
} 